import React from "react";
import { NavLink } from "react-router-dom";
import {Modal, Button,} from 'react-bootstrap'
import {Col,FormGroup,Input,Select,Label,Row, Table} from 'reactstrap';
import './ModalStylee.css';
import { PowerBIEmbed } from 'powerbi-client-react';
import { models } from 'powerbi-client';
import qs from 'querystring';
import Server_connection from '../../constants/constants';

const ViewReport4 = props => {
  const { embedUrl, id} = (props.location) || {};
  return (
    <div className="reportDiv2">
      <div className="reportHeader"><NavLink to="/assignments" activeClassName="active">Go Back</NavLink></div>
      <br/>
      <MyReport4 ReportEmbedUrl = {embedUrl} ReportID = {id}/>
    </div>
  );
};

class MyReport4 extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      id:'',
      embedUrl: '',
      access_token: '',
      value: [],
      message: '',
      searchItem: '',
      isLoaded: false,
    }
    //this.handleUpdateItem = this.handleUpdateItem.bind(this);
    this.onChange = this.onChange.bind(this);
  }

  onChange(e){
    this.setState({[e.target.name]: e.target.value});
    //console.log({[e.target.name]: e.target.value});
  }

  componentWillReceiveProps(nextProps){
      this.setState({
          id: nextProps.id,
          embedUrl: nextProps.embedUrl
      })
  }

  handleSearchInput = (e) => {
      this.setState({ searchItem: e.target.value})
  }

  componentDidMount(){
      fetch(Server_connection.ACCESS_TOKEN_URL)
      .then(res => res.json())
      .then(json => {
        this.setState({
          access_token:  json.access_token,
          isLoaded: true
        })
      })
  }


  render(){
    return(
      <div>
        <PowerBIEmbed
            embedConfig = {{
              type: 'report',   // Supported types: report, dashboard, tile, visual and qna
              id: `${this.props.ReportID}`,
              embedUrl: `${this.props.ReportEmbedUrl}`,
              accessToken: `${this.state.access_token}`,
              tokenType: models.TokenType.Aad,
              settings: {
                panes: {
                  filters: {
                    expanded: false,
                    visible: false
                  }
                },
                // background: models.BackgroundType.Transparent,
              }
            }}

            eventHandlers = { 
              new Map([
                ['loaded', function () {console.log('Report loaded');}],
                ['rendered', function () {console.log('Report rendered');}],
                ['error', function (event) {console.log(event.detail);}]
              ])
            }

            cssClassName = { "Embed-container" }

            getEmbeddedComponent = { (embeddedReport) => {
              window.report = embeddedReport;
            }}
          />
      </div>
      
    );
  }
}

export default ViewReport4;
